<template>
    <div class="">
        <div class="row">
            <div class="col-xs-12">
                <button @click="limpiar" class="btn btn-primary pull-right" data-backdrop="static" data-toggle="modal" data-target="#modal">
                    Nuevo Usuario <i class="fa fa-user-plus"></i>
                </button>
            </div>
        </div>
        <c-data-tables @paginate="send" @refresh="refresh" title="Registro de Usuarios" :limite="[10,20,50,100]" :total="total" :opt="opts">
            <template v-slot:table>
                <table class="table table-bordered table-condensed table-hover table-striped">
            <thead class="text-bold">
                <tr>
                    <th class="col-xs-1">Codigo</th>
                    <th>Tipo</th>
                    <th>Nombre</th>
                    <th>Correo</th>
                    <th></th>
                </tr>
                <tr>
                    <th>
                        <div class="input-group">
                            <input @keyup.enter="send" v-model.number="values.id" type="number" class="form-control input-sm">
                            <span class="input-group-btn">
                                <button type="button" @click="values.id=null" class="btn btn-default btn-sm">
                                    <span class="fa fa-remove"></span>
                                </button>
                            </span>
                        </div>
                    </th>
                    <th>
                        <select @change="send" v-model.number="values.user_type" class="form-control input-sm">
                            <option value="null">Todos</option>
                            <option value="1">Recepcion</option>
                            <option value="2">Quimico</option>
                            <option value="5">Administrador</option>
                        </select>
                    </th>
                    <th>
                        <div class="input-group">
                            <input @keyup.enter="send" v-model="values.name" type="tel" class="form-control input-sm">
                            <span class="input-group-btn">
                                <button type="button" @click="values.name=null" class="btn btn-default btn-sm">
                                    <span class="fa fa-remove"></span>
                                </button>
                            </span>
                        </div>
                    </th>
                    <th>
                        <div class="input-group">
                            <input @keyup.enter="send" v-model="values.email" type="text" class="form-control input-sm">
                            <span class="input-group-btn">
                                <button type="button" @click="values.email=null" class="btn btn-default btn-sm">
                                    <span class="fa fa-remove"></span>
                                </button>
                            </span>
                        </div>
                    </th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(m,i) in model" :key="m.id">
                    <td>{{m.id}}</td>
                    <td>
                        <span v-if="m.user_type==5" class="label label-success">Administrador</span>
                        <span v-if="m.user_type==2" class="label label-success">Quimico</span>
                        <span v-if="m.user_type==1" class="label label-primary">Recepcion</span>
                    </td>
                    <td>{{m.name}}</td>
                    <td>{{m.email}}</td>
                    <td>
                        <button @click="confirmarDelete(m.id,i)" class="btn btn-danger btn-xs"><span class="fa fa-trash"></span></button>
                        <button @click="editar(m,i)" class="btn btn-primary btn-xs"><span class="fa fa-pencil"></span></button>
                    </td>
                </tr>
            </tbody>
        </table>
            </template>
        </c-data-tables>
        <!--agregar nuevo cliente-->
        <t-usuarios :model="client" @store="storeEvent" @update="updateEvent"/>
        <modal-delete :model="confirmar" @onDelete="eliminar"/>

    </div>
</template>

<script>
    import tUsuarios from '@/components/templates/t-usuarios'
    import modalDelete from '@/components/el/c-delete'
    import api from '@/services'
    export default {
        name: "clientes",
        components:{tUsuarios,modalDelete},
        data(){
          return {
              loading:false,
              model:[],
              confirmar:{id:0,index:null},
              edit:null,
              client: {
                  id:0,
                  user_type:null,
                  name:null,
                  email:null,
                  password:null,
                  password_confirmation:null,
              },
              values: {
                  id:null,
                  user_type:null,
                  name:null,
                  email:null
              },
              total:0,
              opts:{
                  per_page:10,
                  page:1,
              }
          }
        },
        mounted(){
            this.$emit('title','Usuarios');
        },
        methods: {
            onSubmit(){
                this.loading = true;
            },
            storeEvent(e){
                this.model.unshift(e);
                this.total +=1;
            },
            updateEvent(m){
                this.model.splice(this.edit,1,m);
                this.edit = null;
                this.limpiar();
            },
            confirmarDelete(id,i){
                this.confirmar.id = id;
                this.confirmar.index = i;
                window.$('#modal-delete').modal({
                    backdrop:false,
                    show:true
                });
            },
            editar(m,i){
                this.setModel(m);
                this.edit = i;
                window.$('#modal').modal('show');
            },
            eliminar(obj){
                api.users.destroy(obj.id).then((r)=>{
                    if (r.data){
                        this.$toast.warning();
                        this.model.splice(obj.index,1);
                    } else {
                        this.$toast.error('No se puedo eliminar el registro');
                    }
                }).catch(()=>{
                    this.$toast.catch();
                });
            },
            setModel(m){
                this.client.id = m.id;
                this.client.user_type = m.user_type;
                this.client.name = m.name;
                this.client.email = m.email;
            },
            limpiar(){
                this.client.id = 0;
                this.client.user_type = null;
                this.client.name = null;
                this.client.email = null;
            },
            //datatables
            send(){
                api.users.search(this.getParams()).then((r)=>{
                    if (r.status ===200){
                        this.model = r.data.data;
                        this.total = r.data.total;
                    }
                }).catch(()=>{});
            },
            refresh(){
                this.values.id = null;
                this.values.user_type = null;
                this.values.name = null;
                this.values.email = null;
                this.send();
            },
            getParams(){
                return {...this.opts,params:this.values};
            }
        }
    }
</script>