<template>
    <c-modal id-modal="modal" :title="model.id>0?'Editar Usuario':'Registrar nuevo Usuario'">
        <template v-slot:body>
            <form role="form" @submit.prevent="onSubmit" @reset="onReset">
                <ValidationObserver ref="form">
                    <div class="box-body">
                        <div class="row">
                            <ValidationProvider vid="user_type" name="tipo de usuario" rules="required" v-slot="{errors}">
                                <div class="form-group col-md-4" :class="{'has-error':errors.length==1}">
                                    <label>Tipo de usuario</label>
                                    <select v-model.number="model.user_type" class="form-control" required>
                                        <option value="1">Recepcion</option>
                                        <option value="2">Quimico</option>
                                        <option value="5">Administrador</option>
                                    </select>
                                    <span class="help-block">{{errors[0]}}</span>
                                </div>
                            </ValidationProvider>
                            <ValidationProvider vid="name" name="nombre" rules="required" v-slot="{errors}">
                                <div class="form-group col-md-8" :class="{'has-error':errors.length==1}">
                                    <label>Nombre de usuario</label>
                                    <input v-model="model.name"  type="text" name="nombre" class="form-control" autofocus required autocomplete="false">
                                    <span class="help-block">{{errors[0]}}</span>
                                </div>
                            </ValidationProvider>
                        </div>
                        <div class="row">
                            <ValidationProvider vid="email" name="correo" rules="required|email" v-slot="{errors}">
                                <div class="form-group col-md-12" :class="{'has-error':errors.length==1}">
                                    <label>Correo</label>
                                    <input v-model="model.email" type="email" name="correo" class="form-control" autofocus required autocomplete="false">
                                    <span class="help-block">{{errors[0]}}</span>
                                </div>
                            </ValidationProvider>
                        </div>
                        <div class="row">
                            <ValidationProvider vid="password" name="contraseña" :rules="`${model.id>0?'':'required'}`" v-slot="{errors}">
                                <div class="form-group col-md-6" :class="{'has-error':errors.length==1}">
                                    <label>Contraseña</label>
                                    <input v-model="model.password" type="password" name="password" class="form-control" autofocus :required="!model.id>0" autocomplete="false"
                                    placeholder="Dejar en blanco para mantener la contraseña actual">
                                    <span class="help-block">{{errors[0]}}</span>
                                </div>
                            </ValidationProvider>
                            <ValidationProvider vid="password_confirmation" name="repetir contraseña" :rules="`${model.id>0?'':'required|confirmed:password'}`" v-slot="{errors}">
                                <div class="form-group col-md-6" :class="{'has-error':errors.length==1}">
                                    <label>Repetir Contraseña</label>
                                    <input v-model="model.password_confirmation" type="password" name="password_confirmation" class="form-control" autofocus :required="!model.id>0" autocomplete="false"
                                    placeholder="Dejar en blanco para mantener la contraseña actual">
                                    <span class="help-block">{{errors[0]}}</span>
                                </div>
                            </ValidationProvider>
                        </div>
                    </div>
                </ValidationObserver>
                <div class="box-footer">
                    <c-btn type="submit" color="btn btn-primary" icon="fa-check" :label="labelSubmit"/>
                    <c-btn type="reset" color="btn btn-danger pull-right" icon="fa-remove" :label="labelReset"/>
                </div>
            </form>
        </template>
    </c-modal>
</template>

<script>
    import api from '@/services'

    export default {
        name: "t-clientes",
        props:{
            loading:{type:Boolean,require:false,default:false},
            modelId:{
                type: Number,
                required: false,
                default: 0
            },
            model:{
                type:Object,
                required: false,
            },
            labelSubmit: {
                type:String,
                required: false,
                default: 'Guardar'
            },
            labelReset: {
                type:String,
                required: false,
                default: 'Cancelar'
            },
            search:{
                type:Boolean,
                required:false,
                default:false
            },
            title:{
                type:String,
                required:false,
                default: 'Registrar nuevo Cliente'
            }
        },
        methods:{
            onSubmit(){
                if (this.search){
                    this.filter();//buscar
                } else{
                    this.validateForm(); //validar y guardar
                }
            },
            validateForm(){
                this.$refs.form.validate().then((success)=>{
                    if (success){
                        //this.$emit('onSubmit',this.model);
                        if (this.model.id>0){
                            this.update();
                        } else this.store();
                        this.$refs.form.reset();
                    }
                });
            },
            filter(){
                this.$emit('search',this.model);
                this.closeModal();
            },
            onReset(){
                this.$refs.form.reset();
                this.closeModal();
            },
            //methods api
            store(){
                api.users.save(this.model).then((r)=>{
                    if (r.status == 201){
                        this.$toast.success();
                        this.$emit('store',r.data.data);
                        this.closeModal();
                    }
                }).catch((err)=>{
                    if (err.response.status === 422){
                        this.$refs.form.setErrors(err.response.data.errors);
                        this.$toast.error();
                    }
                    if (err.response.status>=500){
                        this.$toast.catch();
                    }
                });
            },
            update(){
                api.users.update(this.model).then((r)=>{
                    if (r.status === 200){
                        this.$toast.info();
                        this.$emit('update',r.data.data);
                        this.closeModal();
                    }
                }).catch((err)=>{
                    if (err.response.status === 422){
                        this.$refs.form.setErrors(err.response.data.errors);
                        this.$toast.error();
                    }
                    if (err.response.status>=500){
                        this.$toast.catch();
                    }
                });
            },
            closeModal(){
                window.$('#modal').modal('hide');
            },
        }
    }
</script>